* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #3F4A5A;
  color: rgb(228, 228, 228);
}

header {
  width: 100%;
  padding: 4vh .5%;
}

footer {
  position: relative;
}

main {
  display: flex;
  width: 100%;
}

.menu {
  width: 40%;
}

.page {
  text-align: justify;
  width: 80%;
}