.description {
    padding-right: 15%;
}

.description h1 {
    padding-bottom: 20px;
}

.description article {
    text-indent: 15px;
}

@media(max-width: 1024px) {
    .description h1 {
        font-size: 15px;
    }

    .description {
        width: calc(100%-100px);
        padding-right: 30px;
    }

    .description article {
        font-size: 10px;
        text-indent: 7px;
    }
}